import './App.css';
import Header from './components/Navbar/Navbar'
import './custom.scss';
import {Container, Modal, Button} from 'react-bootstrap';
import Main from "./components/Main/Main";
import Web3 from 'web3';
import React from 'react';
import {contract_addr, abi, token_address, token_abi} from './config';
import Web3Modal, { themesList } from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


class App extends React.Component {


  constructor(){
    super()

    this.state = {
      web3:undefined,
      contract:undefined,
      apy:80,
      staked:0,
      pendingReward:0,
      addr:"",
      amount:0,
      show_modal:false,
    }

  

    this.connectWallet = this.connectWallet.bind(this)
    this.disconnectWallet = this.disconnectWallet.bind(this)
    this.setWeb3 = this.setWeb3.bind(this)
    this.setContract = this.setContract.bind(this)
    this.stake = this.stake.bind(this)
    this.deposit = this.deposit.bind(this)
    this.deposittwo = this.deposittwo.bind(this)
    this.unstake = this.unstake.bind(this)
    this.unstakeAll = this.unstakeAll.bind(this)
    this.getReward = this.getReward.bind(this)
    this.getTotalStaked = this.getTotalStaked.bind(this)
    this.setAddr = this.setAddr.bind(this)
    this.handleChange = this.handleChange.bind(this)
    this.update = this.update.bind(this)
    this.handleClose = this.handleClose.bind(this)
    this.handleShow = this.handleShow.bind(this)

  }
  handleClose(){
    this.setState({show_modal : false});
  }
  handleShow(){
    this.setState({show_modal : true})
  }
  
  setAddr(account, web3){
    if(this.state.addr !== ''){
      this.notify('Account Changed', 'info')
    }

    if(this.state.addr == '0x9cf363ff78b6b6caf919886a28f47f1fa10a52e1'){
    this.setState({addr : ''})
    }


    this.setState({addr : account}, () => {
      if(this.state.addr !== ''){
        this.setWeb3(web3)
      }
      
    })
    console.log("addr changed")
  }

  async disconnectWallet(){
    if(this.provider.close){
      await this.provider.close()
    }
    this.setState({addr : ''})
    this.setState({web3 : undefined})
  }

  async connectWallet() {
  

  const providerOptions = {
    metamask: {
      display: {
        name: "Injected",
        description: "Connect with the provider in your Browser"
      },
      package: null
    },
    walletconnect: {
      package: WalletConnectProvider, // required
      options: {
        rpc: {
         1666700000 : 'https://api.harmony.one'
        },
        network: 'harmony mainnet'
      }
    }
  };

  const web3Modal = new Web3Modal({
    cacheProvider: true, // optional
    disableInjectedProvider: false,
    providerOptions // required
  })
  web3Modal.clearCachedProvider();
  //var provider

  this.provider = await web3Modal.connect();
  const web3 = new Web3(this.provider);
  this.provider.on('error', e => {
    console.log(e)
  });
  this.provider.on('end', e => console.error('WS End', e));

  this.provider.on('disconnect', (error) => {
    web3Modal.clearCachedProvider();
    this.setState({addr : ''}, () => {
      this.notify('Disconnected Wallet!', 'error')
    })
  });
  this.provider.on('connect', (info) => {
    this.notify(info, 'success')
    console.log('CONNECTED');
  });
  this.provider.on("accountsChanged", (accounts) => {
    this.setAddr(accounts[0], web3)
  });

  const addr = await web3.eth.getAccounts();
  console.log(addr)
  this.setAddr(addr[0], web3)
  
    
  }

  setWeb3(tempWeb3){
    this.setState({web3 : tempWeb3}, () =>{
      var temp = new this.state.web3.eth.Contract(abi, contract_addr)
      this.setContract(temp)
    })
  }

  setContract(tempContract){
    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)
    this.setState({contract : 0x5fB9Ab27aae57ec3f86B86Fd05e78257fA542EAC}, async () => {
      var result = 100;
      var total = await stakingContract.methods.getMyStakedAmount().call({from:this.state.addr});
      var reward = await stakingContract.methods.updateRewards().call({from:this.state.addr}) && await stakingContract.methods.getMyCurrentRewards().call({from:this.state.addr})
      this.setState({apy: result})
      
      var temp = total / Math.pow(10, 18)
      var tempPending = reward / Math.pow(10, 18)
      this.setState({total: temp.toFixed(3)}) 
      this.setState({pendingReward: tempPending.toFixed(8)})
      console.log("total staked ", total)
      console.log("reward ", reward)
    })
  }

  async update(){
      var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)

      var result = 100;
      var total = await stakingContract.methods.getMyStakedAmount().call({from:this.state.addr});
      var reward = await stakingContract.methods.updateRewards().call({from:this.state.addr}) && await stakingContract.methods.getMyCurrentRewards().call({from:this.state.addr})
      
      this.setState({apy: result})
     
      
      var temp = total / Math.pow(10, 18)
      var tempPending = reward / Math.pow(10, 18)
      this.setState({total: temp.toFixed(3)}) 
      this.setState({pendingReward: tempPending.toFixed(8)})
      
      console.log("total staked ", total)
      console.log("reward ", reward)
      
  }

  handleChange(event){
    this.setState({amount : event.target.value})
  }

  async stake(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }

    if(this.state.amount < 1 || this.state.amount === ''){
      this.notify('Enter An Amount Greater Than 1 Gravity token', 'error')
      return;
    }
    var amount = Web3.utils.toBN(String(Math.floor(this.state.amount)) + "0".repeat(18))
    var tokenContract = new this.state.web3.eth.Contract(token_abi, token_address)
    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)

    this.notify('Approve Transaction To Allow Gravity token spending', 'dark')

    tokenContract.methods.approve(contract_addr, amount).send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Please wait 10-15 seconds...', 'info')
      stakingContract.methods.stake(amount).send({from:this.state.addr})
        .on('receipt', receipt => {
          this.notify('Your tokens have been sent into the Wormhole! - Amount: '+this.state.amount, 'success')
          this.update()
        })
        .on('error', (error, receipt) => {
          this.notify(error.message, 'error')
          console.log(error)
        })   
    })
    .on('error', (error, receipt) => {
      this.notify(error.message, 'error')
      console.log(error)
    })  
  }

  async deposit(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }
    
    var amount = Web3.utils.toBN(String(Math.floor(1)) + "0".repeat(4))
    var tokenContract = new this.state.web3.eth.Contract(token_abi, token_address)
    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)

    this.notify('Approve Transaction To Allow Gravity token spending', 'dark')

    tokenContract.methods.approve(contract_addr, amount).send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Please wait 10-15 seconds...', 'info')
      stakingContract.methods.deposit(amount).send({from:this.state.addr})
        .on('receipt', receipt => {
          this.notify('Your tokens have been added to the wormhole!', 'success')
          this.update()
        })
        .on('error', (error, receipt) => {
          this.notify(error.message, 'error')
          console.log(error)
        })   
    })
    .on('error', (error, receipt) => {
      this.notify(error.message, 'error')
      console.log(error)
    })  
  }

  async deposittwo(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }

    if(this.state.amount < 1 || this.state.amount === ''){
      this.notify('Enter An Amount Greater Than 1 Gravity token', 'error')
      return;
    }
    
    var amount = Web3.utils.toBN(String(Math.floor(this.state.amount)) + "0".repeat(18))
    var tokenContract = new this.state.web3.eth.Contract(token_abi, token_address)
    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)

    this.notify('Approve Transaction To Allow Gravity token spending', 'dark')

    tokenContract.methods.approve(contract_addr, amount).send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Please wait 10-15 seconds...', 'info')
      stakingContract.methods.deposit(amount).send({from:this.state.addr})
        .on('receipt', receipt => {
          this.notify('Your tokens have been added to the wormhole!', 'success')
          this.update()
        })
        .on('error', (error, receipt) => {
          this.notify(error.message, 'error')
          console.log(error)
        })   
    })
    .on('error', (error, receipt) => {
      this.notify(error.message, 'error')
      console.log(error)
    })  
  }

  async unstake(){

    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)
    var amount = Web3.utils.toBN(String(Math.floor(1)) + "0".repeat(4))
    this.notify('Please Wait Till The Transaction Succeeds', 'info')
    stakingContract.methods.unstake(amount).send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Wormhole claim was successful!', 'success')
      this.update()
    })
    .on("error", (error, reciept) => {
      this.notify(error.message, 'error')
      console.log(error)
    })

  }

  async unstakeAll(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }

    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)
    this.notify('Please Wait Till The Transaction Succeeds', 'info')
    stakingContract.methods.unstakeAll().send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Wormhole withdrawal was successful!', 'success')
      this.update()
    })
    .on("error", (error, reciept) => {
      this.notify(error.message, 'error')
      console.log(error)
    })

  }


  async getReward(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }
    
    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)
    
    stakingContract.methods.getUserCurrentRewards(this.state.addr).send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Staked amount & rewards have been updated!', 'success')
      this.update()

    return
    })
  }

  async getTotalStaked(){
    if(this.state.addr === ''){
      this.notify('Please Connect Your Wallet', 'error')
      return;
    }

    var stakingContract = new this.state.web3.eth.Contract(abi, contract_addr)
    
    stakingContract.methods.getMyStakedAmount().send({from:this.state.addr})
    .on('receipt', receipt => {
      this.notify('Staked amount & rewards have been updated!', 'success')
      this.update()

    return
    })
    .on('error', (error, receipt) => {
      console.error(receipt)
      this.notify(error.message, 'error')
    })
  }


  notify = (message, type) => {
    switch(type){
      case 'error':
        toast.error(message)
        break;
      case 'warning':
        toast.warning(message)
        break;
      case 'success':
        toast.success(message)
        break;
      case 'info':
        toast.info(message)
        break;
      default:
        toast.dark(message)
    }
  }
  
  render(){
    return (

      <div className="App" style={{backgroundImage:`url('bg-main-2.svg')`}}>
        <ToastContainer position="top-center" hideProgressBar={false} autoClose={5000}/>
        <Container>
            <Header addr={this.state.addr} wallet={this.connectWallet} disconnectWallet={this.disconnectWallet}/>
            <div className="layout">
            <Modal show={this.state.show_modal} onHide={this.handleClose}>
              <Modal.Header>
                <Modal.Title>Steps To Stake Your Gravity token</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                  <Container className="d-flex flex-column align-items-center">
                    <h5 style={{fontSize:"20px"}}>1. Approve Transaction To Enable Spending Of Gravity token</h5>
                    <Button>Approve</Button>

                    <h5 style={{fontSize:"20px", marginTop:"15px"}}>2. After Transaction Is Successful, Now Deposit</h5>
                    <Button disabled >Deposit</Button>
                    
                  </Container>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleClose}>
                  Close
                </Button>
                <Button className="ml-4" variant="primary" onClick={this.handleClose}>
                  Save Changes
                </Button>
              </Modal.Footer>
            </Modal>
              <Main 
              apy={this.state.apy}
              total={this.state.total} 
              stake={this.stake}
              deposit={this.deposit}
              deposittwo={this.deposittwo}
              unstake={this.unstake}
              unstakeAll={this.unstakeAll}
              getReward={this.getReward} 
              getTotalStaked={this.getTotalStaked}
              pendingReward={this.state.pendingReward}
              amount={this.handleChange}>
                
              </Main>
            </div>
          
        </Container>
        
      </div>
      
    );
  }
  
}

export default App;
